<template>
  <div class="kuaisulogin">
    <mt-header title="快速注册" id="header">
      <router-link to="/" slot="left" >
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="con">
      <mt-field label="客户姓名" placeholder="请输入用户名" v-model="username"></mt-field>
      <mt-field label="密码" placeholder="请输入密码" type="password" v-model=" password"></mt-field>
      <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
      <mt-field label="身份证号" placeholder="请输入数字" type="number" v-model="number"></mt-field>
      <mt-field label="客户类型" placeholder="钻石会员" ></mt-field>
      <mt-field label="验证码" >
      </mt-field>
      <mt-field label="短信验证" placeholder=""></mt-field>
      <input type="checkbox" >我已经同意以下条款 <a href="#">用户使用协议</a>
      <mt-button type="primary" class="btn" @click="fastLogin">申请注册</mt-button>
    </div>
  </div>
</template>

<script>
  import axios from "axios"
    export default {
        name: "kuaisulogin",
        data:function (){
          return {
            username:'',
            phone:'',
            number:'',
            password:'',

          }

        },
      methods:{
          fastLogin:function () {
            axios.post('/api/user/register', {
              pwd: this.password,
              sex:1,
              type:0,
              idNum:this.number,
              phone:this.phone,
              name:this.username,
              userType:'1'
            },{withCredentials: true})
              .then(function (response) {
                console.log(response);
              })
              .catch(function (error) {
                console.log(error);
              });
          }
      }
    }
</script>

<style scoped>
  .kuaisulogin #header{
    height: 45px;
    background: #006ed8;
  }
  .kuaisulogin img{
    width: 100%;
    height: 151px;
    display: block;
  }
  .kuaisulogin .dengji{
    width: 100%;
    display: block;
    height: 440px;
  }
  .btn{
    width: 100%;
    display: block;
    margin-top: 20px;
  }

</style>



